import { memo, useState } from 'react';
import dynamic from 'next/dynamic';
import { I_Scene } from '@containers/Home/types';
import { ThemeDefaultWrapper } from './styled';

const Header = dynamic(() => import('./Header'));
const Tour = dynamic(() => import('@containers/Home/components/Tour'), {
  ssr: false,
});
const ListScene = dynamic(() => import('./ListScene'));
const Controlbar = dynamic(() => import('./Controlbar'), { ssr: false });

interface Props {
  onNextScene: () => void;
  onChangeScene: (scene: I_Scene) => void;
}

export const ThemeDefault = memo(({ onChangeScene, onNextScene }: Props) => {
  const [isShowListScene, setIsShowListScene] = useState(false);

  return (
    <ThemeDefaultWrapper>
      <Header isShowListScene={isShowListScene} />
      <ListScene
        isShowListScene={isShowListScene}
        setIsShowListScene={setIsShowListScene}
        onChangeScene={onChangeScene}
      />
      <Tour onChangeScene={onChangeScene} onNextScene={onNextScene}>
        <Controlbar />
      </Tour>
    </ThemeDefaultWrapper>
  );
});

export default ThemeDefault;
